.lookbook-preview {
	background: white;
	padding: 20px;
	-webkit-box-shadow: 3px 5px 20px 0 rgba(72,76,145,0.50);
	        box-shadow: 3px 5px 20px 0 rgba(72,76,145,0.50);
	width: 33.33%;
	margin: 40px;
	display: -ms-flexbox;
	display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
    overflow:hidden;
}

.delete-button {
	position: absolute;
	left: 0;
	top: 0;
	padding: 5px;
	cursor: pointer;
}

.delete-button h3 {
	padding: 0;
	margin: 0;
	color: #ccc;
	font-weight: 200;
}

.lookbook-preview h3 {
	color: #686870;
	font-weight: 400;
	margin-bottom: 10px;
}

.lookbook-preview h6 {
	width: 100%;
	text-align: center;
	margin: 5px 0;
	font-weight: 700;
	letter-spacing: 2px;
	text-transform: uppercase;
	color: #5c5d5f;
}

.delete-prompt {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	padding: 20px;
	background: -webkit-linear-gradient(45deg, #D4145A 0%, #FBB03B 100%);
	background: -o-linear-gradient(45deg, #D4145A 0%, #FBB03B 100%);
	background: linear-gradient(45deg, #D4145A 0%, #FBB03B 100%);
	z-index: 9;
	-webkit-transform: translateY(200px);
	    -ms-transform: translateY(200px);
	        transform: translateY(200px);
	opacity: 0;
	visibility: hidden;
}

.delete-prompt p {
	color: white;
	text-align: center;
	font-size: 1.2em;
}

.delete-prompt-button {
  /*background-image: linear-gradient(45deg, #929BEF 0%, #63E2FF 100%);*/
  background: #fff;
  height: 48px;
  /*width: 100%;*/
  margin: 0;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  min-width: 190px;
  font-size: 12px;
  padding: 0px 30px;
  line-height: 46px;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  letter-spacing: 3px;
  cursor: pointer;
  font-weight: 700;
  font-family: 'Avenir';
  margin-top: 15px;
  -webkit-box-shadow: 2px 2px 8px 0px rgba(83, 84, 103, 0.3);
          box-shadow: 2px 2px 8px 0px rgba(83, 84, 103, 0.3);
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
  color: #888;
}

.delete-prompt-button:hover {
  -webkit-transform: translateY(-1px);
      -ms-transform: translateY(-1px);
          transform: translateY(-1px);
  -webkit-box-shadow: 2px 2px 8px 2px rgba(83, 84, 103, 0.3);
          box-shadow: 2px 2px 8px 2px rgba(83, 84, 103, 0.3);
}

@media only screen and (max-width: 700px) {
	.lookbook-preview {
		width: 60%;
	}
}


