@import url('https://fonts.googleapis.com/css?family=Playfair+Display:400,400i,700i|Josefin+Slab:400,400i,700i|Poppins:400,400i,700i');

.wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.primary-button.template-2 {
    background: #fff;
    color: #333;
}

.template-2-container {
    top: 70px;
    left: 70px;
    width: calc(100% - 140px);
    height: calc(100% - 140px);
    position: absolute;
    overflow: hidden;
    font-family: 'Playfair Display', serif;
}

.template-2-container:before {
    content: '';
    width: 100%;
    height: 100%;
    /*background: linear-gradient(white, transparent, transparent);*/
    position: absolute;
    z-index:5;
}

.template-2-header {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 0px;
    z-index: 99;
}

.template-2-header h1 {
    font-size: 3em;
    margin: 0 0 20px 0;
    font-weight: 400;
    color: white;
    background: #222;
    display: inline-block;
    padding: 10px;
}

.template-2-header h6 {
    font-size: 2em;
    font-style: italic;
    font-weight: 400;
    margin: 0 0 1em 0;
    position: absolute;
    right: 5px;
    top: 20px;
}

.template-2-products-wrapper {
    width: 100%;
    height: 100%;
    position: relative;
}

.template-2-product-wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.template-2-product-wrapper.active img {
    opacity: 1;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}

.template-2-product-wrapper img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    position: absolute;
    z-index: 0;
    opacity: 0;
    -webkit-transform: scale(1.08);
        -ms-transform: scale(1.08);
            transform: scale(1.08);
    -webkit-transition: opacity 300ms ease, -webkit-transform 1300ms cubic-bezier(0.01, 1, 0.26, 0.93);
    transition: opacity 300ms ease, -webkit-transform 1300ms cubic-bezier(0.01, 1, 0.26, 0.93);
    -o-transition: opacity 300ms ease, transform 1300ms cubic-bezier(0.01, 1, 0.26, 0.93);
    transition: opacity 300ms ease, transform 1300ms cubic-bezier(0.01, 1, 0.26, 0.93);
    transition: opacity 300ms ease, transform 1300ms cubic-bezier(0.01, 1, 0.26, 0.93), -webkit-transform 1300ms cubic-bezier(0.01, 1, 0.26, 0.93);
}

.template-2-previous {
    /*background: #0a0a0a;*/
    display: inline-block;
    padding: 10px;
    position: absolute;
    color: white;
    font-weight: 400;
    font-size: 3em;
    font-style: italic;
    z-index: 9;
    bottom: 0;
    left: 0;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


.template-2-next {
    /*background: #0a0a0a;*/
    display: inline-block;
    padding: 10px;
    position: absolute;
    color: white;
    font-weight: 400;
    font-size: 3em;
    font-style: italic;
    z-index: 9;
    bottom: 0;
    right: 0;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.template-2-next, .template-2-previous {
    -webkit-transition: all 300ms;
    -o-transition: all 300ms;
    transition: all 300ms;
    text-shadow: #797979 3px 3px 3px;
}

.template-2-next:hover, .template-2-previous:hover {
    -webkit-transform: translateY(-5px);
        -ms-transform: translateY(-5px);
            transform: translateY(-5px);
    color: #dadada;
}


.template-2-product-info {
    position: absolute;
    bottom: 20px;
    background: #222;
    z-index: -10;
    /*transform: translate3d(-50%, -50%, 0);*/
    /*left: 50%;*/
    padding: 25px;
    width: 50%;
    opacity: 0;
    -webkit-transition: opacity 500ms ease-in-out;
    -o-transition: opacity 500ms ease-in-out;
    transition: opacity 500ms ease-in-out;
}

.template-2-product-wrapper.active .template-2-product-info{
    opacity: 1;
    z-index: 10;
}

.template-2-product-info h3 {
    color: white;
    text-transform: uppercase;
    font-size: 2em;
    letter-spacing: 5px;
    font-weight: 400;
    margin-top: 10px;
    margin-bottom: 10px;
}

.template-2-product-info p {
    color: #dadada;
    font-size: 1.2em;
    font-style: italic;
}




