.login-wrapper {
/*	width: 100%;
	height: 100vh;*/
	padding-top: 10%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.login-form {
	background: white;
	padding: 30px 80px;
	/*border-radius: 5px;*/
	margin-bottom: 10%;
	-webkit-box-shadow: 3px 5px 20px 0 rgba(72,76,145,0.50);
	        box-shadow: 3px 5px 20px 0 rgba(72,76,145,0.50);
	text-align: center;
}

.login-form h1 {
	margin-top: 0;
	font-weight: 300;
	text-transform: uppercase;
	letter-spacing: 2px;
	position: relative;
	color: #3b424f;
}

.login-form h1:before {
	content: '';
	height: 6px;
	width: 100%;
	position: absolute;
	bottom: -5px;
	left: 0;
	background: -webkit-linear-gradient(135deg, #929BEF 0%, #63E2FF 100%);
	background: -o-linear-gradient(135deg, #929BEF 0%, #63E2FF 100%);
	background: linear-gradient(315deg, #929BEF 0%, #63E2FF 100%);;
}

