.hero {
	background: white;
	-webkit-box-shadow: 3px 5px 20px 0 rgba(72,76,145,0.50);
	        box-shadow: 3px 5px 20px 0 rgba(72,76,145,0.50);
	padding: 30px;
	margin-top: 30px;
	display: -ms-flexbox;
	display: flex;
}

.hero-image {
	width: 40%;
	background: -webkit-linear-gradient(135deg, #929BEF 0%, #63E2FF 100%);
	background: -o-linear-gradient(135deg, #929BEF 0%, #63E2FF 100%);
	background: linear-gradient(315deg, #929BEF 0%, #63E2FF 100%);
}

.hero-image img {
	width: 100%;
	height: 100%;
	opacity: .7;
}

.hero-content {
	width: 60%;
	padding: 20px 40px;
}

.hero h1 {
	position: relative;
	margin: 0 0 0 10px;
	padding-right: 30px;
	font-size: 2.5em;
	display: inline;
	font-weight: 700;
}

.hero h1:before {
	content: '';
	height: 8px;
	width: 100%;
	position: absolute;
	bottom: -5px;
	right: 0;
	background: -webkit-linear-gradient(135deg, #929BEF 0%, #63E2FF 100%);
	background: -o-linear-gradient(135deg, #929BEF 0%, #63E2FF 100%);
	background: linear-gradient(315deg, #929BEF 0%, #63E2FF 100%);
}

.hero p {
	padding-top: 1.5em;
	font-weight: 200;
	line-height: 2;
	font-size: 1em;
}

@media only screen and (max-width: 900px) {
	.hero-image {
		display: none;
	}
	.hero-content {
		width: 100%;
	}
}


